import Router from "vue-router"
import Login from './login'
import zhuce from './zhuce'
import zhuye from './chongzhi'
export default new  Router({
        routes:[{
        path:'/Login',  
        component:Login
    },
        {
            path:'/zhuce',  
            component:zhuce
        },
        {
            path:'/zhuye',  
            component:zhuye
        }]
    

})